<script>
//import {useTitle} 	from 'vue-page-title';
//import {useRoute} 	from 'vue-router'
import storageManager from '@/plugins/storage';
export default {
	name: 'Lesson Detail',
	components: {
	},
	data() {
		return {
		login: storageManager.get('session','login'),
        configs: storageManager.get('configs'),
        dataItem: this.$route.params.id,
        lesson:[],
        player:[]
		}
	},
	methods: {
        async getData() {
            try {
            this.loading_sources  = false;
            
            const resAPI  = await fetch("https://gateway.cloudrestfulapi.com/api/course/" + this.dataItem, {
                method: 'GET',
                headers: {'Content-Type': 'application/json','client-token-key':this.configs.key},
            });

            const RestReturn   = await resAPI.json();

            if(resAPI.status===200) {
                this.lesson     = RestReturn;
                this.$setPageTitle("หลักสูตร " + this.lesson.name);

                const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/player/query", {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json','client-token-key':this.configs.key},
                    body: JSON.stringify({
                        method: 'find',
                        hidden:  ['lecturer', 'updatedAt'],
                        args: [
                        {
                            $and: [
                                { courseId: RestReturn.master }
                            ]
                        }
                        ]
                    })
                });

                const playerReturn   = await resAPI.json();

                if(resAPI.status===200) {
                    console.log("Load Player Finish")
                    this.player = playerReturn;
                }
            }
            } catch (error) {
            console.log(error)
            }
        },
	},
    async mounted () {
      try {
        await this.getData();
      } catch (error) {
        console.log(Error);
      }
    },
	setup() {
        //const route     = useRoute();
        //const routeName = route.meta.title;
        //useTitle(routeName);
    },
    computed: {
  groupedPlayers() {
    const folders = this.player.filter(p => p.type === 'folder');
    const subItems = this.player.filter(p => p.type !== 'folder');
    const subItemsByFolder = folders.map(folder => ({
      ...folder,
      subItems: subItems.filter(subItem => subItem.root === folder._id)
                        .sort((a, b) => a.order.localeCompare(b.order))
    }));
    const rootItems = subItems.filter(subItem => !subItemsByFolder.some(folder => folder._id === subItem.root))
                              .sort((a, b) => a.order.localeCompare(b.order));
    const allItems = [...subItemsByFolder, ...rootItems]
                        .sort((a, b) => a.order.localeCompare(b.order));
    
    return allItems;
  }
}

};
</script>

